class CourseTime {
  constructor(startAt) {
    this.startAt = new Date(startAt)
  }

  currentTime() {
    return moment()
  }

  startTime() {
    return moment(this.startAt)
  }

  formatedStartTime() {
    return this.startTime().format("YYYY-MM-DD HH:mm:ss")
  }

  diff() {
    let current = this.currentTime().toDate()
    let start = this.startAt
    return current.getTime() - start.getTime()
  }

  getHour() {
    return parseInt(this.diff() / (60 * 60 * 1000))
  }

  diffTime() {
    return moment(this.diff())
  }
}

let course = new CourseTime("2015-12-07T00:50:00")
console.log(course.getHour())
// console.info(current)

let Separator = React.createClass({displayName: "Separator",
  render: function() {
    return (
      React.createElement("span", null, ":")
    )
  }
})

let Second = React.createClass({displayName: "Second",
  getDefaultProps: function(){
    return {second: course.diffTime().second()}
  },
  render: function(){
    return (
      React.createElement("span", null, this.props.second)
    )
  }
})

let Minute = React.createClass({displayName: "Minute",
  getDefaultProps: function(){
    return {minute: course.diffTime().minute()}
  },
  render: function(){
    return (
      React.createElement("span", null, this.props.minute)
    )
  }
})

let Hour = React.createClass({displayName: "Hour",
  getDefaultProps: function(){
    return {hour: course.getHour()}
  },
  render: function(){
    return (
      React.createElement("span", null, this.props.hour)
    )
  }
})

let Timer = React.createClass({displayName: "Timer",

  render: function() {
    return (
    React.createElement("div", null, 
      React.createElement(Hour, null), 
      React.createElement(Separator, null), 
      React.createElement(Minute, null), 
      React.createElement(Separator, null), 
      React.createElement(Second, null)
    )
    )
  }
})

let StartTime = React.createClass({displayName: "StartTime",
  render: function() {
    return (
      React.createElement("div", {className: "start-time"}, 
         course.formatedStartTime() 
      )
    )
  }
})

let Container = React.createClass({displayName: "Container",
  render: function() {
    return (
    React.createElement("div", null, 
      React.createElement(StartTime, null), 
      React.createElement(Timer, null)
    )
    )
  }
})

React.render(
  React.createElement(Container, null),
  document.getElementById('container')
)
